body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(220, 230, 230);
  text-align: center;
  background-size:cover;
  opacity: 0.9;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  max-width: 1080px;
  margin: 20px auto;
  text-align: center;
  background-color: rgb(220, 230, 230);
    text-align: center;
    opacity: 0.7;
    width: 100%;
    height: auto;


}

.body{
    background-color: rgb(220, 230, 230);
}

.top{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.city{
  font-weight: 500;
  font-size: 20px;
  line-height: 1;
  margin: 0;
  letter-spacing: 1px;
}
.city-temp{
  font-weight: 500;
  font-size: 30px;
  line-height: 1;
  margin: 0;
}
button {
    background-color:rgb(185, 189, 185); 
    color: black;
    padding: 5px 15px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 15px;
  }
  .container{
    background-color:rgb(222, 226, 226);
    font-size: 16px;
    text-align: center;
    padding: 5px 15px;
    font-weight: 10px;
    text-decoration: none;
    max-width: 1080px;
    margin: 20px auto;
  }
  .weather-icon{
    width: 40px;
  }
  .icon-img{
    width:200px
  }
  .icon-small{
    width:100px
  }
  .title{
    font-size: 23px;
    font-weight: 700;
  }
  .daily-item{
    background-color: #f5f5f5;
    border-radius: 15px;
    height:40px;
    margin: 5px;
    display: flex;
    align-items: center;
    cursor: pointer;
    font-size: 14px;
    padding: 5px 20px;
  }
  .icon-small{
    width: 40px;
  }
  .day{
    color: #212121;
    flex: 1 1;
    font-weight: 600;
    margin-left: 15px;
  }
  .description{
    flex: 1 1;
    margin-right: 15px;
    text-align: right;
  }
  .min{
    color: #757575;
  }

 

